
import * as React from 'react';
import { styled } from '@mui/system';
import TabsUnstyled from '@mui/base/TabsUnstyled';
import TabsListUnstyled from '@mui/base/TabsListUnstyled';
import TabPanelUnstyled from '@mui/base/TabPanelUnstyled';
import { buttonUnstyledClasses } from '@mui/base/ButtonUnstyled';
import TabUnstyled, { tabUnstyledClasses } from '@mui/base/TabUnstyled';
import LowComponentThree from './../LowComponentThree/LowComponentThree'
import LowComponentTwo from '../LowComponentTwo/LowComponentTwo';
import LowComponent from './../../MainContent/LowComponent/LowComponent';


const blue = {
  50: '#F0F7FF',
  100: '#C2E0FF',
  200: '#ff6584',
  300: '#66B2FF',
  400: '#ff6584',
  500: '#111B47',
  600: '#111B47',
  700: '#0059B2',
  800: '#004C99',
  900: '#003A75',
};

const Tab = styled(TabUnstyled)`
  font-family: Roboto, sans-serif;
  color: white;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: bold;
  background-color: transparent;
  width: 100%;
  padding: 12px 16px;
  margin: 6px 6px;
  border: none;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  
  &:hover {
    background-color: ${blue[400]};
  }

  &:focus {
    color: #fff;
    border-radius: 3px;
    outline: 2px solid ${blue[200]};
    outline-offset: 2px;
  }

  &.${tabUnstyledClasses.selected} {
    background-color: ${blue[50]};
    color: ${blue[600]};
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
    
  }
`;

const TabPanel = styled(TabPanelUnstyled)` 
  width: 100%;
  font-family: Roboto, sans-serif;
  font-size: 0.875rem;

`;

const TabsList = styled(TabsListUnstyled)`

  min-width: 300px;
  background-color: ${blue[500]};
  border-radius: 8px;
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: space-between;
`;

function UnstyledTabsCustomized () {
  return (
        <TabsUnstyled defaultValue={0}>
        <TabsList sx={{display: 'flex', justifyContent: 'center', width: '40%', margin: '0 auto', marginTop: '10vh'}}>
            <Tab>One</Tab>
            <Tab>Two</Tab>
            <Tab>Three</Tab>
        </TabsList>
        <TabPanel value={0} sx={{transition:'2s'}}><LowComponentThree/></TabPanel>
        <TabPanel value={1}> <LowComponentTwo/> </TabPanel>
        <TabPanel value={2}> <LowComponent/> </TabPanel>
        </TabsUnstyled>
  )
}

export default UnstyledTabsCustomized